.woocommerce-cart,
.woocommerce-checkout,
.woocommerce-page,
.woocommerce-order-received {
	
	#main {
		margin: 15px auto;

		p.price,
		span.price {
			font-family: $font__main_jose;
		}

		ul.woocommerce-error {
			list-style: none;
			li {
				padding: 4px 8px;
			}
		}

		.woocommerce-notice,
		.woocommerce-info,
		.woocommerce-message {
			margin: 15px 0;
			padding: 15px;
			color: #333333;
			height: auto;
			background-color: transparent;
			@include prefix( transition, all .3s ease-in-out, o moz webkit );

			a {
				color: rgba( 0, 0, 0, .8 );

				&:hover {
					color: rgba(255, 255, 255, .8);
					@include prefix( text-shadow, 1px 1px 4px rgba(#000, .6 ), o moz webkit );
				}
			}
		}

		.select2-selection--single,
		.select2-container--default {
			border-radius: 2px;
			height: 37px;

			span.select2-selection__rendered {
				padding: 12px 15px;
			}
			span.select2-selection__arrow {

				b {
					margin-top: 8px;
				}
			}
		}

		tr.shipping-methods {

			td {
				text-align: left;
				
				ul#shipping_method {

					label {
						margin-left: 8px;
					}
				}
			}
		}

		/*====================================================
		=            This is for the archive page            =
		====================================================*/
		form.wonka-form-inline {
			justify-content: flex-end;
		}

		p.woocommerce-result-count {
			display: flex;
			align-items: center;
			justify-content: flex-start;
			height: 38px;
		}
		/*=====  End of This is for the archive page  ======*/
		
		/*=====================================================
		=            This is for the checkout page            =
		=====================================================*/
		div.wonka-checkout-row {

			div.checkout-form-left-side {
				margin-bottom: 30px;

				.woocommerce-form-login-toggle {
					position: relative;
					display: flex;
					align-items: center;
					justify-content: flex-end;
					font-weight: 700;
					color: #444444;

				}

				.thwmsc-buttons {
					padding: 0 15px;

					.wonka-btn {
						margin-left: 15px;
					}
				}
			}
		}

        .woocommerce-SavedPaymentMethods-saveNew {
            display: none;
        }

		span.woocommerce-input-wrapper {
			width: 100%;

			input.wonka-form-control {
				width: 100%;
				border-radius: 2px;
			}
		}

		#payment {

			ul {
				list-style: none;
				margin: 0;
				padding: 0 8px;
			}

			input[type=checkbox] {
				margin-right: 8px;
			}

			.woocommerce-terms-and-conditions-wrapper {
				padding: 0 8px;
			}

			ul.payment_methods .stripe-card-group {
				width: 100%;
			}

			.wc-stripe-elements-field {
				width: 100%;
			}
		}

		div#checkout_paypal_message,
		div.wc-amazon-checkout-message {
			display: none;
		}

		.woocommerce-thankyou-order-received {
			padding: 15px 0;
		}

		ul.woocommerce-thankyou-order-details {
			display: flex;
			align-items: center;
			justify-content: space-between;
			flex-wrap: wrap;
			margin: 15px auto;

			li:not(:last-child) {
				border-right: 1px dotted rgba( #444, .6 );
			}

			li {
				padding: 4px 8px 4px 0;
				margin: 8px 8px 8px 0;
				width: auto;

				.thankyou-label {
					display: inline-block;
					text-transform: uppercase;
					width: 100%;
					font-family: $font__main_jose;
				}

				strong {
					display: block;
				}
			}

			@include media-breakpoint-up( md ) {
				
				li {
					width: 19.2%;
				}
			}
		}

		table.woocommerce-table--order-details {
			background: $color__background_alt;
			border-left: 1px dotted rgba( #444, .6 );
			border-right: 1px dotted rgba( #444, .6 ); 

			.product-total {
				text-align: right;
			}

			tfoot {

				td {
					text-align: right;
				}
			}
		}
		div.wonka-row-express-checkout-btns {

            > div:first-child {
                display: flex;
                align-items: center;
                justify-content: center;
                flex-wrap: wrap;
            }

            div.express-btns-text-wrap {
                position: relative;
                width: 100%;
                display: flex;
                align-items: flex-end;
                justify-content: center;
                
                .express-btns-text {
                    position: relative;
                    top: 11px;
                    padding: 2px 15px;
                    background: #ffffff;
                    z-index: 10;
                }
            }

    		div.express-checkout-btns {
    			align-items: center;
    			text-align: center;
    			margin: 0 auto 15px;
                padding: 12px 8px;
                display: flex;
                align-items: center;
                justify-content: center;
                flex-wrap: wrap;
                min-width: 170px;

                > div:not(:last-child) {
                    margin-bottom: 8px;
                }

    			hr {
    				margin: 8px auto;
    				height: 2px;
    				background: rgba( #333, .5 );
    			}
    		}
			
            span.continue-past-btns-text {
                display: inline-block;
                text-align: center;
    			color: #444444;
    			font-weight: 700;
    			padding: 4px 15px;
    		}

            @include media-breakpoint-up( md ) {

                > div {

                    div.express-checkout-btns {

                        > div {
                            margin: 0 4px 0 0 !important;
                        }

                        > div:first-child {
                            margin-left: 4px;
                        }
                    }
                }

				span.continue-past-btns-text {
					white-space: nowrap;
				}
			}
        }
		/*=====  End of This is for the checkout page  ======*/
		
		/*===========================================================
		=            This is the totals on the cart page            =
		===========================================================*/
		span.dropdown-wrapper {
			width: 100%;
		}

		a.continue-shopping {
			margin: 15px 0;	
			display: inline-block;
		}

		.woocommerce-cart-form {

			table.table {

				td.product-thumbnail {

					&:before {
						display: none;
					}
				}
			}
		}

		div.cart-collaterals {
			position: relative;
			text-align: right;

			table {
				padding: 0 15px;

				thead {

					tr {

						th {
							text-align: left;
						}
					}
				}

				tbody {

					tr {

						th {
							text-align: left;
						}

						td {
							text-align: right;
						}

                        &.shipping-methods {

                            td {
                                text-align: left;
                            }
                        }

						&.cart-promo {

							div.activate-panel {
								cursor: pointer;

								.panel-title {
									position: relative;
									display: flex;
									align-items: center;
									justify-content: space-between;

									&:after {
										content: "+";
										display: inline-block;
										@include font-size( 1.6 );
										@include prefix( transition, all .3s ease-in-out, o moz webkit );
									}
								}

								&[aria-expanded=true] {

									.panel-title {

										&:after {
											content: "-";
										}
									}
								}
							}

							div#collapseOne {

                                input {
                                    width: 55%;
                                    line-height: 1.1;
                                    padding: 8px;
                                    border-radius: 100px 0 0 100px;
                                    @include font-size( 1.1 );

                                    &::placeholder {
                                        font-style: italic;
                                    }
                                }

                                button.wonka-btn {
                                    margin-left: -3px;
                                    padding: 8px;
                                    border-radius: 0 100px 100px 0;
                                    line-height: 1.1;
                                    @include font-size( 1.1 );
                                }
                            }
						}
					}
				}
			}

			a.wonka-btn {
				position:relative;
				display: inline-block;
				margin: 5px auto;
				width: 100%;
				text-align: center;
			}

			#pay_with_amazon {
				margin: 8px auto;
				text-align: center;
			}
		}
		/*=====  End of This is the totals on the cart page  ======*/
		&.main-checkout {

			.entry-header {
				display: none;
			}

			div.express-checkout-btns {
				position: relative;
				border-radius: 4px;
				border: 1px solid rgba( #000, .3 );
				margin: 15px 0;
				min-height: 50px;
				text-align: center;

				span.express-btns-text {
					position: relative;
					width: 165px;
					margin: auto;
					top: -25px;
					display: inline-block;
					padding: 8px 15px;
					background: #ffffff;
				}
			}

            .cart-promo {

                div#accordion {

                    div.activate-panel {
                        cursor: pointer;

                        .panel-title {
                            position: relative;
                            display: flex;
                            align-items: center;
                            justify-content: space-between;

                            &:after {
                                content: "+";
                                display: inline-block;
                                @include font-size( 1.6 );
                                @include prefix( transition, all .3s ease-in-out, o moz webkit );
                            }
                        }

                        &[aria-expanded=true] {

                            .panel-title {

                                &:after {
                                    content: "-";
                                }
                            }
                        }
                    }

                }

                div#collapseOne {

                    input {
                        width: 55%;
                        line-height: 1.1;
                        padding: 8px;
                        border-radius: 100px 0 0 100px;
                        @include font-size( 1.1 );

                        &::placeholder {
                            font-style: italic;
                        }
                    }

                    button.wonka-btn {
                        margin-left: -3px;
                        padding: 8px;
                        border-radius: 0 100px 100px 0;
                        line-height: 1.1;
                        @include font-size( 1.1 );
                    }
                }
            }
		}

		&.main-page {
			max-width: 1180px;

			ul {
				list-style: none;
				margin: 0;
				padding: 0;
			}

			header.entry-header {
				padding: 15px;
			}

			div.entry-content {
				margin: 0;
				padding: 15px;
			}
		}

		&.main-my-account {

			label.woocommerce-form__label-for-checkbox {
				margin: 15px 0;
				width: 100%;
			}
		}
	}

	&.archive {

		main.main-product {
			max-width: 1180px;
		}

		div.wonka-row {

			ul.products {

				.star-rating {
					display: none;
				}
			}
		}
	}
}

body.woocommerce-edit-account {

    .wonka-form-row.first-row {
        margin-bottom: 15px;
    }
}

body.woocommerce-checkout,
body.woocommerce-cart {

    div#primary {

    	main#main {

    		article.page {

        		header.entry-header {
        			display: none;
        		}
        	}
        }
    }
}